import React from 'react';
import { Button, Subtitle } from '../styledComponents/StyledH2.js';


const H2 = ({ tasks, toggleHideDone, hideDone, setAllDone }) => {
  return (
    tasks.length > 0 && (
      <Subtitle className="section__subtitle">
        Lista zada≈Ñ
        <Button onClick={toggleHideDone}> {hideDone ? "Poka≈º" : "Ukryj"} uko≈Ñczone</Button>

        <Button disabled={tasks.every(({ done }) => done)} onClick={setAllDone} >Uko≈Ñcz wszystkie</Button>
      </Subtitle>
    ));
}

export default H2;